import {Box, Grid, SvgIcon} from "@mui/material";
import ThermostatIcon from "@mui/icons-material/Thermostat";
import AirIcon from "@mui/icons-material/Air";
import FilterDramaIcon from "@mui/icons-material/FilterDrama";
import {
  TireRepairRounded,
  WaterDropRounded,
  WaterRounded,
  WbSunnyRounded
} from "@mui/icons-material";

/**
 * WeatherConditionItem props
 */
export type WeatherConditionItemType = {
  type: string;
  title: string;
  value: string;
};

/**
 * WeatherConditionItem Component to show individual weather condition
 *
 * @param props {WeatherConditionItemType} - WeatherConditionItem props
 * @returns
 */
const WeatherConditionItem = (props: WeatherConditionItemType) => {
  let iconContent;

  if (props.type === "temperature")
    iconContent = <ThermostatIcon sx={{fontSize: "18px"}} />;
  else if (props.type === "wind")
    iconContent = <AirIcon sx={{fontSize: "18px"}} />;
  else if (props.type === "clouds")
    iconContent = <FilterDramaIcon sx={{fontSize: "18px"}} />;
  else if (props.type === "pressure")
    iconContent = (
      <SvgIcon component={TireRepairRounded} sx={{fontSize: "18px"}} />
    );
  else if (props.type === "humidity")
    iconContent = <SvgIcon component={WaterRounded} sx={{fontSize: "18px"}} />;
  else if (props.type === "precipitation")
    iconContent = (
      <SvgIcon component={WaterDropRounded} sx={{fontSize: "18px"}} />
    );
  else if (props.type === "uvIndex")
    iconContent = (
      <SvgIcon component={WbSunnyRounded} sx={{fontSize: "18px"}} />
    );

  return (
    <Grid
      item
      xs={2}
      sx={{
        padding: "0",
        height: "80px"
      }}
    >
      <Grid
        item
        xs={12}
        display="flex"
        alignItems="center"
        justifyContent="center"
        sx={{
          width: "100%",
          height: "40px",
          flexDirection: {xs: "column", sm: "row"}
        }}
      >
        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            color: "rgba(255, 255, 255, .7)",
            padding: 0
          }}
        >
          {iconContent}
        </Box>
        <Box
          sx={{
            color: "rgba(255, 255, 255, .7)",
            fontSize: {xs: "10px", sm: "12px", md: "14px"},
            paddingLeft: {xs: "0px", sm: "4px", md: "6px"},
            paddingTop: {xs: "2px", sm: "0px"},
            display: "flex",
            alignItems: "center"
          }}
        >
          {props.title}
        </Box>
      </Grid>
      <Grid
        item
        xs={12}
        display="flex"
        alignItems="center"
        justifyContent="center"
        sx={{height: "40px"}}
      >
        <Box
          sx={{
            fontFamily: "Poppins",
            fontWeight: "600",
            fontSize: {xs: "12px", sm: "14px", md: "16px"},
            color: "white",
            lineHeight: 1
          }}
        >
          {props.value}
        </Box>
      </Grid>
    </Grid>
  );
};

export default WeatherConditionItem;
